import React, { useState, useEffect } from 'react';
import { useStructure } from '@unexp/router'
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import { ConfigProvider, AdaptivityProvider, AppRoot, Root, Panel } from '@vkontakte/vkui'

import Home from './panels/Home';
import Shop from './panels/Shop';
import Info from './panels/Info';

const App = () => {
	let structure = useStructure({ view: 'home', panel: 'home' })
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	const [properties, setProperties] = useState([{ vk: undefined, balance: undefined, rub: undefined, points: undefined, alert: undefined, vip: {} }])
	const API_KEY = "C4D52C8426DAC";

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			const api_url = await fetch(`https://likebot.ru/data?token=${API_KEY}&id=${user.id}`);
			const data = await api_url.json();
			setPopout(null); // обнуление спинера


			setProperties({
				vk: data.vk,
				balance: data.balance,
				rub: data.rub,
				points: data.points,
				alert: data.alert,
				vip: data.vip
			});
		}

		fetchData();
	}, [properties]);

	return (
		<ConfigProvider isWebView={true}>
			<AdaptivityProvider >
				<AppRoot>

					<Root popout={structure.popout} activeView={structure.view}>

						<View id='home' activePanel={structure.panel} // View Home
							popout={popout}>
							<Panel id="home">
								<Home id='home' fetchedUser={fetchedUser} properties={properties} />
							</Panel>
						</View>

						<View id='info' activePanel={structure.panel}
						>
							<Panel id="info">
								<Info id='info' fetchedUser={fetchedUser} properties={properties} />
							</Panel>
						</View>

						<View id='shop' activePanel={structure.panel}
						>
							<Panel id="shop">
								<Shop id='shop' API_KEY={API_KEY} fetchedUser={fetchedUser} properties={properties} />
							</Panel>
							<Panel id="first">
								<Shop id='first' API_KEY={API_KEY} fetchedUser={fetchedUser} properties={properties} />
							</Panel>
							<Panel id="apart">
								<Shop id='apart' API_KEY={API_KEY} fetchedUser={fetchedUser} properties={properties} />
							</Panel>
						</View>


					</Root>


				</AppRoot>
			</AdaptivityProvider>
		</ConfigProvider>
	);
}


export default App;

