import React from 'react';
import { Group, Cell, Header, Counter, List } from '@vkontakte/vkui';
import { Icon20DonateCircleFillYellow, Icon20MoneyCircleOutline } from '@vkontakte/icons';


const Status = ({ properties }) => {
    return (
        <Group info="Information for user" mode="card">
            <List> <Header>Ваше состояние</Header>
                <Cell before={<Icon20DonateCircleFillYellow />} indicator={<Counter mode="primary">{properties.balance}</Counter>}>Баллы</Cell>
                <Cell before={<Icon20MoneyCircleOutline />} indicator={<Counter mode="primary">{properties.rub}</Counter>}>Рубли</Cell>
            </List>
        </Group>
    )
}

export default Status;
