import React from 'react';
import { useRouter, usePopout } from '@unexp/router'
import { Alert, Panel, PanelHeaderBack, PanelHeader, View, Group, Header, Counter, HorizontalCell, Card, CardGrid, ContentCard, HorizontalScroll, Div, Button } from '@vkontakte/vkui';
import { Icon20LikeCircleFillRed, Icon20MoneyCircleOutline, Icon20ArticleBoxOutline, Icon20DonateCircleFillYellow } from '@vkontakte/icons';
import axios from 'axios';
import Status from './Status';
import img_first from '../img/img_first.jpg'; // Картинка для товара "Первое"
import img_apart from '../img/img_apart.jpg'; // Картинка для товара "Отдельное"

const largeImageStyles = { // Стиль
	width: 220,
	height: 124,
	borderRadius: 8,
	border: '1px solid var(--placeholder_icon_background)',
	objectFit: 'cover'
};
const price_first = 1000; // На сколько изменять число баллов
const price_alert = 1500;
const Shop = ({ id, properties, API_KEY }) => {
	let { push, back, replace } = useRouter();
	let { setPopout, closePopout } = usePopout();

	/* Функция продажи */
	async function market(api, id, price, currency, position) {
		await axios.get(`https://likebot.ru/market?token=${api}&id=${id}&price=${price}&currency=${currency}&position=${position}`)
			.then(async function (response) {
				if (response.status === 204) { // Если баллов нет
					return setPopout(
						<Alert
							actions={[
								{ title: 'Как получить баллы?', action: () => replace({ view: "info", panel: "info" }), mode: 'destructive' },
								{ title: 'Понятно', autoclose: true, mode: 'cancel' },
							]}
							actionsLayout='vertical'
							onClose={closePopout}
							header='Отказано!'
							text='У Вас недостаточно баллов, подкопите и повторите попытку'
						/>
					)
				}
				if (response.status === 206) { // Если рублей нет
					return setPopout(
						<Alert
							actions={[
								{ title: 'Пополнить', action: () => replace({ view: "info", panel: "info" }), mode: 'destructive' },
								{ title: 'Отмена', autoclose: true, mode: 'cancel' },
							]}
							actionsLayout='vertical'
							onClose={closePopout}
							header='Отказано!'
							text='Вам необходимо пополнить счёт'
						/>
					)
				}
				console.log(response.status); // Status ответа
				console.log(response.data); // Что отвечает сервер
				setPopout(
					<Alert
						actions={[
							{ title: 'Супер!', mode: 'cancel', autoclose: true },
						]}
						actionsLayout='vertical'
						onClose={closePopout}
						header='Успех'
						text='Вы успешно купили ЛТ, скоро Вы будете на стеночке'
					/>
				)
			})
			.catch(function (err) {
				console.log("ERROR SYKa BLYAT: ", err)
			})
	}

	return (

		<View activePanel={id}>

			<Panel id="shop">
				<PanelHeader left={<PanelHeaderBack onClick={back} />}>
					Магазин
        </PanelHeader>
				<Status properties={properties}></Status>
				<Group header={<Header aside={<Icon20LikeCircleFillRed />}>Like Time</Header>} mode="card">
					<HorizontalScroll>
						<div style={{ display: 'flex' }}>
							<HorizontalCell onClick={() => push({ panel: 'first' })} size='l' header='Первое' subtitle='1OOO баллов'>
								<img alt="lt" style={largeImageStyles}
									src={img_first} />
							</HorizontalCell>
							<HorizontalCell onClick={() => push({ panel: 'apart' })} size='l' header='Отдельное' subtitle='15OO баллов'>
								<img alt="lt" style={largeImageStyles}
									src={img_apart} />
							</HorizontalCell>
						</div>
					</HorizontalScroll>
				</Group>
			</Panel>

			<Panel id="first">
				<PanelHeader left={<PanelHeaderBack onClick={back} />}>
					Товар
</PanelHeader>
				<Group info="Information for user">
					<Header aside={<Icon20ArticleBoxOutline />}>Описание</Header>
					<CardGrid size="l">
						<Card>
							<ContentCard
								image={img_first}
								header="Первое ЛТ"
								text="Вы будете опубликованы Первым в посту"
								caption="Лайки идут к Вам на страничку"
								mode="outline"
								maxHeight={240}
							/>
						</Card>
					</CardGrid>
				</Group>
				<Status properties={properties}></Status>
				<Group>
					<Group mode="card">
						<Header>Купить</Header>
						<Div style={{ display: 'flex' }}>
							<Button onClick={async () => { (market(API_KEY, properties.vk, price_first, "bal", "first")) }} before={<Icon20DonateCircleFillYellow />} size="l" stretched mode="commerce" after={<Counter>1000</Counter>}>За баллы</Button>
							<Button onClick={async () => { (market(API_KEY, properties.vk, 10, "rub", "first")) }} before={<Icon20MoneyCircleOutline />} size="l" stretched mode="commerce" after={<Counter>10</Counter>}>За рубли</Button>
						</Div>
					</Group>
				</Group>
			</Panel>


			<Panel id="apart">
				<PanelHeader left={<PanelHeaderBack onClick={back} />}>
					Товар
</PanelHeader>
				<Group info="Information for user">
					<Header aside={<Icon20ArticleBoxOutline />}>Описание</Header>
					<CardGrid size="l">
						<Card>
							<ContentCard
								image={img_apart}
								header="Отдельное ЛТ"
								text="Вы будете опубликованы Отдельным в посту"
								caption="Лайки идут к Вам на страничку"
								mode="outline"
								maxHeight={240}
							/>
						</Card>
					</CardGrid>
				</Group>
				<Status properties={properties}></Status>
				<Group>
					<Group mode="card">
						<Header>Купить</Header>
						<Div style={{ display: 'flex' }}>
							<Button onClick={async () => { (market(API_KEY, properties.vk, price_alert)) }} before={<Icon20DonateCircleFillYellow />} size="l" stretched mode="commerce" after={<Counter>1500</Counter>}>За баллы</Button>
							<Button onClick={() =>
								setPopout(
									<Alert
										actions={[
											{ title: 'Хорошо..', mode: 'cancel', autoclose: true },
										]}
										actionsLayout='vertical'
										onClose={closePopout}
										header='В разработке'
										text='Мы ещё работаем над этой функцией'
									/>
								)
							}
								before={<Icon20MoneyCircleOutline />} size="l" stretched mode="commerce" after={<Counter>10</Counter>}>За рубли</Button>
						</Div>
					</Group>
				</Group>
			</Panel>


		</View>

	)
};

/* 
					<Button onClick={async () => { (balanceChanger(API_KEY, properties.vk, properties.balance, change, "give")) }} size="l" mode="commerce"/*<- Делает зеленой before={<Icon28MarketAddBadgeOutline />} stretched /*<- растягивает >Купить</Button>
*/

export default Shop;
