import React from 'react';
import { useRouter } from '@unexp/router'
import PropTypes from 'prop-types';
import { Counter, List, View } from '@vkontakte/vkui'
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import { Icon28MarketLikeOutline, Icon28ListLikeOutline, Icon20DonateCircleFillYellow, Icon20MoneyCircleOutline, Icon20FavoriteCircleFillYellow, Icon20NarrativeOutline } from '@vkontakte/icons'


const Home = ({ fetchedUser, properties, id }) => {
	let { push } = useRouter();
	return (
		<View activePanel={id}>
			<Panel id="home">
				<PanelHeader>Ваша информация</PanelHeader>
				{fetchedUser &&
					<Group title="User Data Fetched with VK Bridge and API DataBase">
						<Cell
							before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200} /> : null}
							description={properties.vk && `🆔 ${properties.vk}`}
							indicator={properties.vip?.access ? `👑 VIP 👑` : null}
						>

							{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
						</Cell>
					</Group>}

				<Group title="Navigation Block">
					<Div>
						<Cell size="xl" onClick={() => push({ view: 'info', panel: 'info' })} expandable before={<Icon28ListLikeOutline />}>За что дают баллы?</Cell>
						<Cell size="xl" onClick={() => push({ view: 'shop', panel: 'shop' })} expandable before={<Icon28MarketLikeOutline />}>Магазин</Cell>
					</Div>
				</Group>
				<Group info="Information for user">
					<List>
						<Cell size="xl" before={<Icon20DonateCircleFillYellow />} indicator={<Counter mode="primary">{properties.balance}</Counter>}>Баллы</Cell>
						<Cell before={<Icon20MoneyCircleOutline />} indicator={<Counter mode="primary">{properties.rub}</Counter>}>Рубли</Cell>
						<Cell size="xl" before={<Icon20FavoriteCircleFillYellow />} indicator={<Counter mode="primary">{properties.points}</Counter>}>Заработано баллов сегодня</Cell>
						<Cell before={<Icon20NarrativeOutline />} indicator={<Counter mode="primary">{properties.alert ? 'Вкл' : 'Выкл'}</Counter>}>Уведомления о поступлении баллов</Cell>
					</List>
				</Group>
			</Panel>
		</View>
	)
};

Home.propTypes = {
	id: PropTypes.string.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
	properties: PropTypes.shape({
		balance: PropTypes.number,
		rub: PropTypes.number,
		points: PropTypes.number,
		alert: PropTypes.bool,
		vk: PropTypes.number.isRequired,

	})
};

export default Home;
