import React from 'react';
import { useRouter } from '@unexp/router'
import { SimpleCell, InfoRow, Header, View, Panel, PanelHeader, Group, PanelHeaderBack, Cell, List, Counter } from '@vkontakte/vkui'
import { Icon16LikeOutline, Icon16Comment } from '@vkontakte/icons';


const Info = ({ id }) => {
    let { back } = useRouter();

    return (
        <View activePanel={id}>
            <Panel id="info">
                <PanelHeader left={<PanelHeaderBack onClick={back} />}>
                    Получить
        </PanelHeader>

                <Group>
                    <Header aside="Баллы" mode="secondary">Вознаграждения</Header>
                    <SimpleCell multiline>
                        <InfoRow header="За последний пост"></InfoRow>
                        <List >
                            <Cell before={<Icon16LikeOutline />} indicator={<Counter mode="prominent">25</Counter>}>Лайк не в числе первых без VIP</Cell>
                            <Cell before={<Icon16LikeOutline />} indicator={<Counter mode="prominent">5O</Counter>}>Лайк среди 20 первых без VIP</Cell>
                            <Cell before={<Icon16LikeOutline />} indicator={<Counter mode="prominent">5O</Counter>}>Лайк не в числе первых с VIP</Cell>
                            <Cell before={<Icon16LikeOutline />} indicator={<Counter mode="prominent">1OO</Counter>}>Лайк среди 20 первых с VIP</Cell>
                            <InfoRow header="На любом посту"></InfoRow>
                            <Cell before={<Icon16Comment />} indicator={<Counter mode="prominent">3</Counter>}>Комментарий к посту без VIP</Cell>
                            <Cell before={<Icon16Comment />} indicator={<Counter mode="prominent">5</Counter>}>Комментарий к посту с VIP</Cell>
                        </List>
                    </SimpleCell>
                </Group>
            </Panel>
        </View>
    )
};


export default Info;
